import { Component } from "react";
import Showlist from "./list.ele.show";

class Inputfeild extends Component {
  constructor() {
    super();
    this.state = {
      inputvalue: ""
    };
    
  }
  onchangeHandler(e) {
    return this.setState(
      {
        inputvalue: e.target.value,
      }
    );
  }
  submitinput(e)
  {
    e.preventDefault();
    var input_value=this.state.inputvalue;
    var list_=[]; 
    var a=JSON.parse(localStorage.getItem("main_lis")); 
    if ( a=== null)
    {
        console.log("yes it null");
        localStorage.setItem("main_lis", JSON.stringify(list_))  ;
        var copy_of_local_str_list = JSON.parse(
          localStorage.getItem("main_lis")
        );
        /// pushing elemen in local storage
        copy_of_local_str_list.push(input_value);
        //       send the element in local storage list
        localStorage.setItem(
          "main_lis",
          JSON.stringify(copy_of_local_str_list)
        );
        console.log("we set a new list in local storage");

    }
    else{
        console.log('first value input');
        var copy_of_local_str_list = JSON.parse(
          localStorage.getItem("main_lis")
        );
       /// pushing elemen in local storage
        copy_of_local_str_list.push(input_value);
//       send the element in local storage list
        localStorage.setItem("main_lis", JSON.stringify(copy_of_local_str_list)); 
       console.log("we set a new list in local storage");
    }
    this.setState({
        inputvalue:''
    })
  }
  render() {
    return (
      <div className="Con">
        <form  className="Form" onSubmit={this.submitinput.bind(this)}>
          <input
            value={this.state.inputvalue}
            onChange={this.onchangeHandler.bind(this)}
          />
          <button id="formbutton" type="submit">Add</button>
        </form>
        <Showlist />
      </div>
    );
  }
}
export default Inputfeild