import { type } from "@testing-library/user-event/dist/type";



function Button({props,pros_2}) {

  var line_through = pros_2;
  function eventHandler(e) {

    var a = e.target.parentNode.parentNode;
    





    var b = a.childNodes[0];
    b.style.textDecoration = "line-through";

    if (!line_through.includes(b.id))
    {
        line_through.push(b.id);
    }
    
    // line_through.push(b.id);
    console.log(line_through);
    localStorage.setItem("line_through", JSON.stringify(line_through));
   
  }
  return (
    <button id="twos_1" onClick={eventHandler}>
      Done
    </button>
  );
}
export default Button